import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from './../views/Home'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/sale',
    name: 'Sale',
    component: () => import('./../views/Sale')
  },
  {
    path: '/cart',
    name: 'Cart',
    component: () => import('./../views/Cart')
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
